'use client';

import { useEffect, useRef, useState } from "react";

interface Message {
  role: 'system' | 'user' | 'assistant';
  content: string;
}

interface MeasurementMetadata {
  source_file: string;
  expected_bpm: number;
  expected_episodes: number;
  expected_anomalies: string[];
}

interface MeasurementPayload {
  metadata: MeasurementMetadata;
  request_body: Record<string, unknown>;
}

export default function App() {
  const [history, setHistory] = useState<Message[]>([]);
  const [message, setMessage] = useState("");
  const [busy, setBusy] = useState(false);
  const [activeFile, setActiveFile] = useState<string | null>(null);
  const bottomRef = useRef<HTMLDivElement>(null);

  // Auto-scroll
  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [history]);

  // Unified function to send messages or data
  async function callApi(userMessage: string | null, observation: Record<string, unknown> | null) {
    if (busy) return;
    try {
      setBusy(true);
      
      if (userMessage) {
        setHistory(prev => [...prev, { role: "user", content: userMessage }]);
        setMessage("");
      }

      const res = await fetch("/api/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
            message: userMessage, 
            history: history,
            observation: observation 
        }),
      });

      if (!res.ok) throw new Error(`API failed: ${res.status}`);
      const data = await res.json();

      setHistory(data.history);
    } catch (err: unknown) {
      if (err instanceof Error) {
        alert("Error: " + err.message);
      } else {
        alert("An unknown error occurred");
      }
    } finally {
      setBusy(false);
    }
  }

  const sendRandomMeasurement = async () => {
    const randomIndex = Math.floor(Math.random() * 10);
    const filename = `measurement_${randomIndex}.json`;
    
    try {
        setBusy(true);
        const res = await fetch(`/measurements/${filename}`);
        if (!res.ok) throw new Error("Failed to fetch measurement");
        const item: MeasurementPayload = await res.json();
        
        setActiveFile(item.metadata.source_file);
        
        const observation = item.request_body;
        if (!observation.device) {
          observation.device = { 
            userAgent: navigator.userAgent, 
            platform: navigator.platform 
          };
        }
        
        setBusy(false);
        callApi(null, observation);
    } catch (err: unknown) {
        if (err instanceof Error) {
          alert("Failed to send measurement: " + err.message);
        } else {
          alert("An unknown error occurred while sending measurement");
        }
        setBusy(false);
    }
  };

  function handleKey(e: React.KeyboardEvent) {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      if (message.trim()) callApi(message, null);
    }
  }

  return (
    <div className="flex flex-col h-screen bg-[#F8FAFC] font-sans text-slate-900">
      {/* Header */}
      <header className="flex items-center justify-between px-8 py-4 bg-white border-b border-slate-200 shadow-sm">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 bg-blue-600 rounded-lg flex items-center justify-center">
            <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
            </svg>
          </div>
          <div>
            <h1 className="text-xl font-bold text-slate-800 tracking-tight">Cardio<span className="text-blue-600">AI</span></h1>
            <p className="text-xs text-slate-500 font-medium uppercase tracking-wider">Clinical Decision Support System</p>
          </div>
        </div>
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2 px-3 py-1 bg-green-50 text-green-700 rounded-full border border-green-100 text-sm font-medium">
            <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
            System Active
          </div>
        </div>
      </header>

      <main className="flex flex-1 overflow-hidden">
        {/* Sidebar */}
        <aside className="w-80 bg-white border-r border-slate-200 flex flex-col p-6 gap-6 overflow-y-auto">
          <div>
            <h3 className="text-sm font-semibold text-slate-400 uppercase tracking-wider mb-4">Patient Data</h3>
            <button 
              onClick={sendRandomMeasurement}
              disabled={busy}
              className="w-full py-3 px-4 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-300 text-white rounded-xl font-semibold transition-all shadow-md shadow-blue-100 flex items-center justify-center gap-2"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-3 7h3m-3 4h3m-6-4h.01M9 16h.01" />
              </svg>
              New Analysis
            </button>
          </div>

          {activeFile && (
            <div className="bg-slate-50 rounded-xl p-4 border border-slate-100">
              <h4 className="text-xs font-bold text-slate-400 uppercase mb-2">Active Source</h4>
              <p className="text-sm font-mono text-slate-600 break-all">{activeFile}</p>
            </div>
          )}

          <div className="mt-auto">
            <button 
              onClick={() => { setHistory([]); setActiveFile(null); }}
              disabled={busy}
              className="w-full py-2 px-4 text-slate-500 hover:text-red-600 hover:bg-red-50 rounded-lg text-sm font-medium transition-all flex items-center justify-center gap-2"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
              </svg>
              Clear Session
            </button>
          </div>
        </aside>

        {/* Chat Area */}
        <section className="flex-1 flex flex-col bg-[#F1F5F9] relative">
          <div className="flex-1 overflow-y-auto p-8 space-y-6">
            {history.length === 0 ? (
              <div className="h-full flex flex-col items-center justify-center text-center max-w-md mx-auto space-y-4">
                <div className="w-20 h-20 bg-white rounded-3xl shadow-xl flex items-center justify-center mb-4">
                  <svg className="w-10 h-10 text-blue-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M8 10h.01M12 10h.01M16 10h.01M9 16H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-5l-5 5v-5z" />
                  </svg>
                </div>
                <h2 className="text-2xl font-bold text-slate-800">Welcome to CardioAI</h2>
                <p className="text-slate-500">Initialize the diagnostic process by sending a measurement for real-time AI analysis.</p>
              </div>
            ) : (
              history
                .filter(m => m.role !== "system")
                .map((m, i) => (
                  <div key={i} className={`flex ${m.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                    <div className={`max-w-[80%] rounded-2xl p-5 shadow-sm border ${
                      m.role === 'user' 
                        ? 'bg-blue-600 text-white border-blue-500 rounded-tr-none' 
                        : 'bg-white text-slate-700 border-slate-200 rounded-tl-none'
                    }`}>
                      <div className={`text-[10px] uppercase font-bold tracking-widest mb-1 ${
                        m.role === 'user' ? 'text-blue-200' : 'text-slate-400'
                      }`}>
                        {m.role === 'user' ? 'Practitioner' : 'AI Arrhythmologist'}
                      </div>
                      <div className="text-[15px] leading-relaxed whitespace-pre-wrap font-medium">
                        {m.content}
                      </div>
                    </div>
                  </div>
                ))
            )}
            {busy && (
              <div className="flex justify-start animate-pulse">
                <div className="bg-white border border-slate-200 rounded-2xl rounded-tl-none p-5 shadow-sm">
                  <div className="flex gap-2">
                    <div className="w-2 h-2 bg-slate-300 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-slate-300 rounded-full animate-bounce [animation-delay:-.3s]"></div>
                    <div className="w-2 h-2 bg-slate-300 rounded-full animate-bounce [animation-delay:-.5s]"></div>
                  </div>
                </div>
              </div>
            )}
            <div ref={bottomRef} />
          </div>

          {/* Input Bar */}
          <div className="p-8 bg-gradient-to-t from-[#F1F5F9] via-[#F1F5F9] to-transparent">
            <form 
              onSubmit={(e) => { e.preventDefault(); if (message.trim()) callApi(message, null); }}
              className="max-w-4xl mx-auto flex items-end gap-3 bg-white p-3 rounded-2xl shadow-lg border border-slate-200"
            >
              <textarea
                placeholder="Request diagnostic clarification..."
                value={message}
                disabled={busy}
                onChange={(e) => setMessage(e.target.value)}
                onKeyDown={handleKey}
                className="flex-1 bg-transparent border-none focus:ring-0 text-slate-700 placeholder-slate-400 resize-none py-2 px-3 min-h-[44px] max-h-32 text-sm font-medium"
              />
              <button 
                type="submit" 
                disabled={busy || !message.trim()}
                className="w-11 h-11 flex items-center justify-center bg-blue-600 hover:bg-blue-700 disabled:bg-slate-200 text-white rounded-xl transition-all shadow-md shadow-blue-100 shrink-0"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
                </svg>
              </button>
            </form>
          </div>
        </section>
      </main>
    </div>
  );
}
